import React from "react";
import styled from "styled-components";
import FadeIn from "react-fade-in";
import posed from "react-pose";
import Layout from "../components/Layout/home";
import background from "../components/Layout/home/images/background.png";
import bottles from "../components/Layout/products/images/bottlesTwo.png";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";

const Box = posed.div({
  hoverable: true,
  pressable: true,
  init: {
    scale: 1,
  },
  hover: {
    scale: 1.2,
  },
  press: {
    scale: 1.1,
  },
});

const HomePage = () => {
  return (
    <Layout>
      <FadeIn>
        <Wrap>
          <div>
            <Fade top>
              <Header>Vodka Cruiser</Header>
            </Fade>
            <Fade bottom>
              <Text>Stay Colourful</Text>
            </Fade>
          </div>
          <Background src={background} alt={background} />
          <AnimationWrap>
            <Box>
              <Zoom top>
                <Bottles src={bottles} alt={bottles} />
              </Zoom>
            </Box>
          </AnimationWrap>
        </Wrap>
        <BottomWrap />
      </FadeIn>
    </Layout>
  );
};

export default HomePage;

const Wrap = styled.div`
  background-color: #243a76;
  position: relative;
  height: 82.5vh;
  flex-wrap: wrap;
  align-items: center;
  display: flex;
  justify-content: center;
`;

const Background = styled.img`
  top: 4rem;
  width: 94%;
  height: 94%;
  position: absolute;
  @media (max-width: 830px) {
    top: 3rem;
  }
`;

const Header = styled.h1`
  font-weight: 900;
  font-size: 3.5em;
  line-height: 120%;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 7rem;
  @media (max-width: 1350px) {
    font-size: 3em;
  }
  @media (max-width: 1160px) {
    font-size: 2.5em;
  }
  @media (max-width: 820px) {
    font-size: 2.25em;
  }
  @media (max-width: 750px) {
    font-size: 2em;
  }
  @media (max-width: 670px) {
    font-size: 1.5em;
  }
  @media (max-width: 470px) {
    font-size: 1.25em;
  }
  @media (max-width: 380px) {
    font-size: 1.125em;
  }
  @media (max-width: 350px) {
    font-size: 1em;
  }
`;

const Text = styled.h2`
  font-weight: 500;
  font-size: 2em;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.05em;
  color: #ffcb05;
  margin-top: -2rem;
  @media (max-width: 1350px) {
    font-size: 1.7em;
  }
  @media (max-width: 1160px) {
    font-size: 1.5em;
    margin-top: -1rem;
  }
  @media (max-width: 820px) {
    font-size: 1.25em;
  }
  @media (max-width: 750px) {
    font-size: 1em;
  }
  @media (max-width: 670px) {
    font-size: 0.8em;
  }
`;

const BottomWrap = styled.div`
  background: #ffff;
  position: fixed;
  height: 20vh;
  width: 100%;
`;

const Bottles = styled.img`
  width: 30rem;
  @media (max-width: 820px) {
    width: 20rem;
  }
`;

const AnimationWrap = styled.div`
  position: absolute;
  z-index: 1;
  top: 70%;
  @media (max-width: 820px) {
    top: 80%;
  }
`;
